var browser = require('bowser');

var prefix =
  browser.webkit ? '-webkit-' :
  browser.gecko ? '-moz-' :
  '';

var config = {
  DEFAULTS: {},
  MAPBOX_API_ACCESS_TOKEN: null,
  CURSOR: {
    GRABBING: prefix + 'grabbing',
    GRAB: prefix + 'grab'
  }
};

module.exports = config;



